<template>
    <el-card class="material-management">
        <!-- 一级/二级/新增 -->
        <el-row class="mb20 pl20">
            <el-col :span="7">
                <span>一级分类：</span>
                <el-select
                    v-model="materialGroup.level1"
                    placeholder="请选择一级分类"
                    @change="classifyChange"
                >
                    <!-- TODO: 此处需要循环 -->
                    <el-option label="手机" value="手机" :style="{'min-width': minWidth + 'px'}"></el-option>
                    <el-option label="配件" value="配件"></el-option>
                </el-select>
            </el-col>
            <el-col :span="8">
                <span>二级分类：</span>
                <el-select v-model="materialGroup.level2" placeholder="请选择二级分类" >
                    <!-- TODO: 此处需要循环 -->
                    <el-option label="手机" value="手机" :style="{'min-width': minWidth + 'px'}"></el-option>
                    <el-option label="配件" value="配件"></el-option>
                </el-select>
            </el-col>
            <el-col :span="9">
                <el-button
                    type="primary"
                    @click="newIncrease"
                    icon="el-icon-circle-plus-outline"
                >新增物料组</el-button>
            </el-col>
        </el-row>

        <!-- 表格 -->
        <el-table
            class="fg1"
            :data="tableData.table"
            border
            stripe
            v-loading="!tableData.table.length ? true : false"
        >
            <el-table-column type="index"></el-table-column>
            <el-table-column align="center" prop="number" label="物料组 ID"></el-table-column>
            <el-table-column align="center" prop="name" label="物料组名称"></el-table-column>
            <el-table-column align="center" prop="scmNumber" label="SCM物料组 ID"></el-table-column>
            <el-table-column align="center" label="操作">
                <template slot-scope="scope">
                    <el-tooltip content="修改" placement="top" :enterable="false">
                        <el-button
                            type="primary"
                            size="mini"
                            icon="el-icon-edit"
                            @click="modify(scope.row)"
                        ></el-button>
                    </el-tooltip>
                    <el-tooltip content="查看详情" placement="top" :enterable="false">
                        <el-button
                            @click="handleClick(scope.row)"
                            type="warning"
                            size="mini"
                            icon="el-icon-view"
                        ></el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>

        <!-- 分页 -->
        <div class="paging mt10">
            <el-pagination
                background
                layout="prev, pager, next, jumper"
                :total="tableData.total"
                @current-change="currentChange"
            ></el-pagination>
        </div>

        <!-- 新增物料组 -->
        <el-dialog
            :title="title"
            :visible.sync="dialogFormVisible"
            :show-close="disabled"
            :close-on-click-modal="disabled"
            :close-on-press-escape="disabled"
            @closed="disabled = false"
        >
            <el-form
                :model="materialGroup"
                :rules="rules"
                ref="materialGroup"
                :disabled="disabled"
                @submit.native.prevent="onSubmit"
            >
                <!-- TODO: 插值表达式中的值应该是获取的 -->
                <el-row class="pl40">
                    <el-col :span="6">
                        <el-form-item label="一级分类：">{{ materialGroup.level1 }}</el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="二级分类：">{{ materialGroup.level2 }}</el-form-item>
                    </el-col>
                </el-row>

                <el-form-item label="SCM物料组 ID" prop="number" label-width="130px">
                    <el-input v-model="materialGroup.number" :disabled="scmDsiabled"></el-input>
                </el-form-item>

                <el-form-item label="物料组名称" label-width="130px" prop="name">
                    <el-input v-model="materialGroup.name"></el-input>
                </el-form-item>

                <el-row :gutter="15">
                    <el-col :span="!disabled ? 20 : 24">
                        <el-form-item label="物料组描述" label-width="130px" prop="describe">
                            <el-input v-model="materialGroup.describe"></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :span="4" v-show="!disabled">
                        <p
                            class="mt15 copy"
                            @click="materialGroup.describe = materialGroup.name"
                        >从名称复制</p>
                    </el-col>
                </el-row>

                <el-form-item label="颜色" label-width="130px" prop="colors">
                    <div class="df aic item">
                        <el-input
                            class="colorInput"
                            v-model="materialGroup.colors[index]"
                            v-for="(item, index) in materialGroup.colors"
                            :key="index"
                        >
                            <i
                                slot="suffix"
                                class="el-input__icon el-icon-circle-close"
                                v-show="!disabled"
                                @click="materialGroup.colors.length > 1 && materialGroup.colors.splice(index, 1)"
                            ></i>
                        </el-input>
                        <i
                            class="el-icon-circle-plus-outline ml15"
                            @click="materialGroup.colors.push('')"
                            v-show="!disabled"
                        ></i>
                    </div>
                </el-form-item>

                <el-form-item label="配置(储存)" label-width="130px" prop="configurations">
                    <div class="df aic item">
                        <el-input
                            class="colorInput"
                            v-model="materialGroup.configurations[index]"
                            v-for="(item, index) in materialGroup.configurations"
                            :key="index"
                        >
                            <i
                                slot="suffix"
                                class="el-input__icon el-icon-circle-close"
                                v-show="!disabled"
                                @click="materialGroup.configurations.length > 1 && materialGroup.configurations.splice(index, 1)"
                            ></i>
                        </el-input>

                        <i
                            class="el-icon-circle-plus-outline ml15"
                            @click="materialGroup.configurations.push('')"
                            v-show="!disabled"
                        ></i>
                    </div>
                </el-form-item>

                <el-form-item label="版本" label-width="130px" prop="versions">
                    <div class="df aic item">
                        <el-input
                            class="colorInput"
                            v-model="materialGroup.versions[index]"
                            v-for="(item, index) in materialGroup.versions"
                            :key="index"
                        >
                            <i
                                slot="suffix"
                                class="el-input__icon el-icon-circle-close"
                                v-show="!disabled"
                                @click="materialGroup.versions.length > 1 && materialGroup.versions.splice(index, 1)"
                            ></i>
                        </el-input>

                        <i
                            class="el-icon-circle-plus-outline ml15"
                            @click="materialGroup.versions.push('')"
                            v-show="!disabled"
                        ></i>
                    </div>
                </el-form-item>

                <el-form-item label="包装清单" label-width="130px">
                    <el-input v-model="materialGroup.listing"></el-input>
                </el-form-item>

                <el-form-item label="物料参数" label-width="130px" style="width: 490px">
                    <el-table :data="materialGroup.parameter" border size="mini">
                        <el-table-column prop="item" label="参数项" width="180"></el-table-column>
                        <el-table-column label="参数值" width="180">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.value" size="mini" />
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>

                <el-form-item label="详情页(电商图)" label-width="130px" prop="fileList">
                    <el-upload
                        action="https://jsonplaceholder.typicode.com/posts/"
                        list-type="picture-card"
                        :on-preview="handlePictureCardPreview"
                        :file-list="materialGroup.fileList"
                        :on-remove="handleRemove"
                        :on-change="({name, url}) => materialGroup.fileList.push({name, url})"
                    >
                        <!-- :on-success="hangleSuccess" -->
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog title="图片详情" :visible.sync="details" :append-to-body="true">
                        <img width="100%" :src="detailsImageUrl" alt />
                    </el-dialog>
                </el-form-item>

                <el-form-item style="text-align: right" v-show="!disabled">
                    <el-button @click="cancel">取 消</el-button>
                    <el-button type="primary" native-type="submit">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </el-card>
</template>

<script>
/* el-select BUG */
import { mixin } from "../../mixins/resize";

export default {
    mixins: [mixin],
    data() {
        /**
         *  判断 颜色/配置/版本
         */
        let validatePass = (rule, value, callback) => {
            if (value.some(item => item === "")) {
                callback(new Error("参数不能为空"));
            } else {
                callback();
            }
        };
        return {
            /* 表格数据 */
            tableData: {
                total: null,
                table: []
            },
            /* 新增物料 对话框 */
            dialogFormVisible: false,
            title: "新增物料组",
            /* 图片详情 */
            details: false,
            detailsImageUrl: "",
            /* 是否禁用 */
            disabled: false,
            scmDsiabled: false,
            /* 新增物料组 */
            materialGroup: {
                number: 1016007801000100,
                level1: "手机",
                level2: "配件",
                name: "苹果手机 iPhone 11 Pro ",
                describe: "Apple iPhone 11 Pro ",
                colors: ["绿色", "翡翠绿"],
                configurations: ["64G"],
                versions: ["aa"],
                fileList: [
                    { name: "aa.jpg", url: require("@/assets/img/avatar.png") }
                ],
                parameter: [{ item: "CPU", value: "" }],
                listing: ""
            },
            rules: {
                number: [
                    {
                        required: true,
                        message: "请填写物料组编号",
                        trigger: "blur"
                    }
                ],
                name: [
                    {
                        required: true,
                        message: "请填写物料组名称",
                        trigger: "blur"
                    }
                ],
                describe: [
                    {
                        required: true,
                        message: "请填写物料组描述",
                        trigger: "blur"
                    }
                ],
                colors: [
                    {
                        required: true,
                        validator: validatePass,
                        trigger: "blur"
                    }
                ],
                configurations: [
                    {
                        required: true,
                        validator: validatePass,
                        trigger: "blur"
                    }
                ],
                versions: [
                    {
                        required: true,
                        validator: validatePass,
                        trigger: "blur"
                    }
                ],
                fileList: [
                    {
                        type: "array",
                        required: true,
                        message: "至少上传一张图片",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    methods: {
        /**
         *  TODO: 新增物料组/同时获取分类和物料参数信息
         */
        newIncrease() {
            this.title = "新增物料组";
            this.dialogFormVisible = true;
        },
        /**
         *  图片展示
         */
        handlePictureCardPreview(file) {
            this.detailsImageUrl = file.url;
            this.details = true;
        },
        /**
         *  图片删除
         */
        handleRemove(file) {
            let {
                materialGroup: { fileList }
            } = this;
            let index = fileList.findIndex(({ uid }) => uid === file.uid);
            fileList.splice(index, 1);
        },
        /**
         *  TODO: 获取表格数据/以及分类数据获取
         */
        async _getMaterial() {
            const { res, total } = await this.$get("/getMaterial");
            this.tableData.total = total;
            this.tableData.table = res;
            console.log(res);
        },
        /**
         *  TODO: 二级分类数据获取
         */
        classifyChange(value) {
            console.log(value);
        },
        /**
         *  TODO: 分页
         */
        async currentChange(value) {
            const { res } = await this.$get(`/getMaterial?page=${value}`);
            this.tableData.table = res;
        },
        /**
         *  TODO: 查看详情
         */
        handleClick(row) {
            console.log(row);

            this.title = "详情";
            this.disabled = true;
            this.dialogFormVisible = true;
        },
        /**
         *  TODO: 修改
         */
        modify(row) {
            this.title = "物料组修改";
            this.dialogFormVisible = true;
            this.scmDsiabled = true;
        },
        /**
         *  取消新增
         */
        cancel() {
            this.$confirm("确定取消?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                this.dialogFormVisible = false;
                this.scmDsiabled = false;
                this.$refs.materialGroup.resetFields();
                this.$message({
                    type: "success",
                    message: "已取消"
                });
            });
        },

        /**
         *  TODO: 提交/提交时校验
         */
        onSubmit() {
            this.$refs.materialGroup.validate(valid => {
                if (valid) {
                    this.dialogFormVisible = false;
                    this.scmDsiabled = false;
                    this.$refs.materialGroup.resetFields();
                    alert("此处提交数据!");
                } else {
                    return false;
                }
            });
        }
    },
    created() {
        this._getMaterial();
    }
};
</script>

<style scoped>
.material-management {
    color: #606266;
    background-color: #fff;
}

.paging {
    text-align: right;
}

.copy {
    cursor: pointer;
    color: #909399;
}

.item {
    flex-wrap: wrap;
}

.colorInput {
    width: 100px;
    margin-right: 5px;
    margin-bottom: 2px;
}

/* ELEMENT_UI STYLE */
.material-management /deep/ .el-button {
    font-size: 14px;
}

.item /deep/ .el-icon-circle-plus-outline {
    font-size: 30px;
    cursor: pointer;
}
</style>



